import Util from '../util/util'

class Player {
    constructor() {

    }

    static Random() {
        const player = new Player();
        player.age = Util.randAge(18, 22);
        player.info = Util.randInfo();
        player.head = Util.randImg();
        player.name = Util.randName();
        return player;
    }
}

export {Player}