<template>
    <div id="team">
        <el-row :gutter="20">
            <el-col :span="6" v-for="p in players">
                <el-card :body-style="{ padding: '0px' }">
                    <img :src="require('../assets/' + p.head + '.png')" class="image" />
                    <div style="padding: 14px;">
                        <span>{{ p.name }}</span>
                        <div class="bottom clearfix">
                            <el-tooltip class="item" effect="dark" :content="p.info" placement="bottom">
                                <el-button>详情</el-button>
                            </el-tooltip><br />
                            <el-button type="text" class="button" @click="sell(p)">出售</el-button>
                        </div>
                    </div>
                </el-card>
            </el-col>
        </el-row>
        <el-button @click="generate()">随机生成</el-button>
    </div>
</template>

<script>
    import {Player} from '../core/player'
    import {game} from '../core/game'

    export default {
        name: "MyTeam",
        data() {
            return {
                players: [],
                game: {}
            }
        },
        mounted() {
            this.game = game;
        },
        methods: {
            generate: function () {
                this.$prompt('请输入球员名字', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                }).then(({ value }) => {
                    const p = Player.Random();
                    p.name = value;
                    this.players.push(p);
                }).catch(() => {
                });
            },
            sell: function (player) {
                this.game.gold += 20;
                this.players.splice(this.players.indexOf(player), 1);
            }
        }
    }
</script>

<style scoped>
    .image {
        margin-top: 10px;
        width: 64px;
        height: 64px;
    }

    .el-card {
        margin-bottom: 20px;
    }
</style>